body {
  font-family: "DM Sans", sans-serif;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  overscroll-behavior: none;
}

.section {
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
}

.container {
  width: 360px;
  z-index: 1;
}

.section--one {
  margin-left: 15%;
}

.section--two {
  margin-right: 10%;
}

.section--third {
  margin-left: 10%;
}

.first h1 {
  font-size: 10rem;
  line-height: 8rem;
  letter-spacing: -0.3rem;
  width: 50%;
  margin: 0 0 30px -10px;
}

.button--hero {
  background: #801b2b;
  color: white;
  border: none;
  font-weight: 600;
  border-radius: 50px;
  padding: 15px 30px;
  margin-top: 24px;
  margin-right: 40px;
  font-size: 0.6rem;
  cursor: pointer;
  transition: all 0.8s ease;
}
.button--hero:hover {
  background: black;
  color: white;
}

.button--navbar {
  background: black;
  color: white;
  border: none;
  border-radius: 50px;
  padding: 10px 30px;
  margin-right: 40px;
  font-size: 0.6rem;
}

.second {
  justify-content: flex-end;
  position: relative;
}

h2 {
  font-size: 3rem;
  letter-spacing: -2px;
  line-height: 2.5rem;
}

#webgi-canvas {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
}

#webgi-canvas-container {
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  position: fixed;
}
